.selectField {transition: all .1s linear; padding: 10px; min-width: 100px; border: solid 1px #cccccc; background-color: #ffffff; border-radius: 10px; position: relative; text-align: left;}
.selectField:hover {border-radius: 10px 10px 0 0;}
.selectField ul {transition: all .1s linear; border: solid 1px #ffffff; border-style: none; border-radius: 0 0 10px 10px; position: absolute; top: 40px; left: -1px; right: -1px; max-height: 0px; overflow: hidden; background-color: #ffffff; margin: 0; padding: 0; cursor: pointer;}
.selectField:hover ul {max-height: 100px; border: solid 1px #cccccc;}
.selectField li {list-style: none; margin: 0; padding: 0 10px;}
.selectField li:hover {background-color: #cccccc;}
.selectField li:last-child {margin-bottom: 10px;}

.tokenImg {width: 20px; height: 20px; display: block; margin-left: 10px; float: right;}
.tokenEth {background: url('asset/token-eth.svg') no-repeat center; background-size: contain;}
.tokenCel {background: url('asset/token-cel.png') no-repeat center; background-size: contain;}
.tokenDai {background: url('asset/token-dai.png') no-repeat center; background-size: contain;}
.tokenOmg {background: url('asset/token-omg.png') no-repeat center; background-size: contain;}